.navbar {
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    list-style: none;
    background-color: rgba(0, 0, 0, 0.875);
    box-shadow: 0px 5px 10px black;
    height: 4rem;
    color: azure;
    text-align: center;
    gap: 2vw;
}

.navbar li {
    font-family: 'Caveat', cursive;
    font-size: 18px;
    line-height: 3.9rem;
    width: 5.2rem;
}

.navbar li:hover {
    box-shadow: inset 2px 2px rgba(112, 52, 52, 0.747), inset -5px -5px rgba(72, 36, 36, 0.586);
    background-color: rgb(213, 1, 1);
    cursor: pointer;
}